@import '../../../style.scss';
.post{
    @include themify($themes){
        -webkit-box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.09);
        -moz-box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.09);
         box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.09);
         border-radius: 20px;
         background-color: themed(bg);
         color: themed(textColor);
    .container{
        padding: 20px;
        
        .user{
            display: flex;
            align-items: center;
            justify-content: space-between;
            

            .userInfo{
                display: flex;
                gap: 20px;
                img{
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                    object-fit: cover;

                }
                .detail{
                   display: flex;
                   flex-direction: column;
                   .name{
                    font-weight: 500;

                   }
                   .date{ font-size: 12px;}
                }
               
            }
        }
        .content{
            margin: 20px 0;
            p{margin-bottom: 20px;}
            img{
                width: 100%;
                max-height: 500px;
                object-fit: cover;
            }
        }
        .info{
            display: flex;
            align-items: center;
            gap: 20px;
            .item{
                display: flex;
                align-items: center;
                gap: 10px;
                cursor: pointer;
                font-size: 14px;
            }
        }
    }
}
}