.login{
    height: 100vh;
    background-color: rgb(193,190,255);
    display: flex;
    align-items: center;
    justify-content: center;
    .card{
        width: 50%;
        background-color: #fff;
        display: flex;
        min-height: 600px;
        border-radius: 10px;
        overflow: hidden;
        .left{
            flex: 1;
            background:linear-gradient(rgb(39,11,96,0.5), rgb(39,11,96,0.5)), url("https://images.pexels.com/photos/3228727/pexels-photo-3228727.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1") center;
            background-size: cover;
            padding: 50px;
            display: flex;
            flex-direction: column;
            gap: 30px;
            color: #fff;
           
           h1{
            font-size: 100px;
            line-height: 100px;
           }
            p{}
            span{
                font-size: 14px;
            }
            button{
                width: 50%;
                padding: 5px;
                border: none;
                background-color: #fff;
                color:rebeccapurple ;
                font-weight: bold;
                cursor: pointer;
            }
        }
        .right{
            flex: 1;
            padding: 50px;
            display: flex;
            flex-direction: column;
            gap: 50px;
            justify-content: center;
            h1{
                color: #555;
            }
            form{
                display: flex;
                flex-direction: column;
                gap: 30px;
            }
            input{
                border: none;
                border-bottom: 1px solid lightgray;
                padding: 20px;
            }
            button{
                width: 50%;
                padding: 10px;
                border: none;
                background: #938eef;
                color: #fff;
                font-weight: bold;
                cursor: pointer;
            }
        }
    }
}